.container{
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}

.movies {
    display: grid; 
    border-color: black;
    grid-template-columns: repeat(2, minmax(400px, 2fr)); 
    grid-gap: 100px; 
    padding: 20px; 
    width: 80%; 
    padding-top: 70px; 
}

@media screen and (max-width: 1090px) {
    .movies{
        grid-template-columns: 1fr;
        width: 100%;
    }
    
}